@import url(https://fonts.googleapis.com/css?family=Raleway:300,400,700&display=swap);
/* display */
.d_inline_block {
	display: inline-block;
}

/* vertical-align */
.v_md {
	vertical-align: middle;
}

/* Portfolio */
.portfolio {
	width: calc(33.3% - 8px);
	margin-bottom: 15px;
	display: inline-block;
	font-size: 15px;
}
.portfolio:nth-child(3n - 1) {
	margin: 0 12px;
}
.portfolio .portfolio_img img {
	vertical-align: middle;
}
.portfolio .portfolio_img img:hover {
	opacity: 0.6;
}
.portfolio_info {
	background-color: #fff;
	padding: 15px;
}
.portfolio_info p {
	margin: 0;
}
.portfolio_info p:nth-child(2) {
	margin-top: 15px;
	line-height: 1.5;
}
@media (max-width: 600px) {
	.portfolio {
		width: 100%;
		display: block;
		margin-bottom: 15px;
	}
	.portfolio:nth-child(3n - 1) {
		margin: 0;
	}
}

/* pagelist*/
.page_list {
	margin: 32px 0;
	text-align: center;
}
.page_list div {
	padding: 8px 16px;
	display: inline-block;
	vertical-align: middle;
}
.page_list .arrow {
	font-size: 15px;
}
.page_list .insert {
	color: #fff;
	background-color: #000;
}
.page_list .page:hover {
	color: #fff;
	background-color: #000;
}
.page_list .insert:hover {
	color: #000;
	background-color: #ccc;
}

/* parttitle */
.part_title {
	margin: 10px 0;
	font-weight: 400;
}

/* technicalskills */
.SkillsBar span {
	font-size: 15px;
	display: inline-block; /* 마진겹침현상때문에   */
	margin: 15px 0;
}
.SkillsBar .skillbar_body {
	width: 100%;
	height: 38px;
	line-height: 38px;
	background-color: #9e9e9e;
}
.SkillsBar .skillbar_body .skillbar_percent {
	background-color: #616161;
	text-align: center;
	color: #fff;
	height: 100%;
	font-size: 15px;
}

.layout {
  width: 100vw;
  height: 100vh;
  font-size: 20px;
  overflow: auto;
  position: relative;
  background-color: #f1f1f1;
}

.container {
  background-size: cover;
  margin: auto;
  /* overflow: auto; */
}

.container .wrapper {
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 100vh;
}

/* SideMenu */
.sideMenu {
  background-color: #fff;
  width: 300px;
  height: 100vh;
  padding: 16px;
  position: relative;
  box-sizing: border-box;
  line-height: 36px;
  position: fixed;
  z-index: 300;
  -webkit-animation: animateleft 0.4s;
          animation: animateleft 0.4s;
  transition: 0.5s;
  -webkit-transition: 0.5s;
  -moz-transition: 0.5s;
  -ms-transition: 0.5s;
  -o-transition: 0.5s;
}
@-webkit-keyframes animateleft {
  from {
    left: -300px;
    opacity: 0;
  }
  to {
    left: 0;
    opacity: 1;
  }
}
@keyframes animateleft {
  from {
    left: -300px;
    opacity: 0;
  }
  to {
    left: 0;
    opacity: 1;
  }
}

.sideMenu .close_ico {
  width: 20px;
  height: 20px;
  position: absolute;
  top: 10px;
  right: 10px;
}

.sideMenu .profile_img {
  width: 120px;
  height: 120px;
  margin-bottom: 30px;
  border-radius: 4px;
  -webkit-border-radius: 4px;
  -moz-border-radius: 4px;
  -ms-border-radius: 4px;
  -o-border-radius: 4px;
}

.sideMenu .title {
  font-size: 20px;
  font-weight: bolder;
}

.sideMenu .noti {
  font-size: 15px;
  color: #757575;
  margin-bottom: -10px;
}

.sideMenu .menu_list ul {
  padding: 0;
}

.sideMenu .menu_list ul li {
  list-style: none;
  cursor: pointer;
}

.sideMenu .menu_list ul li a {
  text-decoration: none;
  color: #000;
}

.sideMenu .menu_list ul li span {
  display: inline-block;
  font-size: 15px;
  vertical-align: middle;
}

.sideMenu .menu_list ul li .ico {
  width: 15px;
  height: 15px;
  line-height: 15px;
  margin-right: 15px;
  display: inline-block;
  vertical-align: middle;
}

.sideMenu .sns_list span {
  width: 18px;
  height: 18px;
  margin-right: 10px;
  display: inline-block;
  cursor: pointer;
}

.sideMenu .sns_list span:last-child {
  margin-right: 0px;
}

/* Header */
.header {
  margin-top: 10px;
  padding: 0 16px;
  padding-bottom: 15px;
}
.header .top {
  height: 65px;
  position: relative;
}
.header .top .menu_m {
  font-size: 30px;
  position: absolute;
  top: 0;
  left: 0;
}
.header .top .profile_m {
  width: 65px;
  height: 65px;
  position: absolute;
  top: 0;
  right: 0;
  border-radius: 50%;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  -o-border-radius: 50%;
}
.header .title {
  font-weight: bold;
  font-size: 36px;
  margin-top: 10px;
  margin-bottom: 40px;
}
.header .header_nav {
  padding-bottom: 15px;
  border-bottom: 6px solid #999;
}
.header .header_nav .header_nav_title,
.header .header_nav > button {
  display: inline-block;
  vertical-align: middle;
  font-size: 15px;
  margin-right: 5px;
  border: none;
}
.header .header_nav > button:last-child {
  margin-right: 0;
}
.header .header_nav button {
  padding: 8px 15px;
  background-color: #fff;
}
.header .header_nav button:hover {
  background-color: #999;
  cursor: pointer;
}
.header .header_nav button .filter_ico {
  width: 15px;
  height: 15px;
  margin-right: 15px;
  display: inline-block;
  vertical-align: middle;
}
.header .header_nav .insert {
  color: #fff;
  background-color: black;
}

/* overlay */
.overlay {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-color: #000;
  opacity: 0.3;
  display: none;
}
@media (min-width: 993px) {
  .header .top {
    display: none;
  }
  .sideMenu .close_ico {
    display: none;
  }
}

@media (max-width: 995px) {
  .sideMenu {
    display: block;
  }
  .header .top {
    display: none;
  }
}
@media (max-width: 992px) {
  .sideMenu {
    display: none;
  }
  .header .top {
    display: block;
  }
  .sideMenu .close_ico {
    display: block;
  }
}
/* Footer */
.footer {
  background-color: #616161;
  color: #fff;
  padding-top: 12px;
}
.footer .footer_box {
  padding: 32px 16px;
}
.footer .footer_box .content_box {
  width: 33.3%;
  padding: 0 8px;
  box-sizing: border-box;
  display: inline-block;
  vertical-align: top;
}
.footer .footer_box .content_box .title {
  font-size: 24px;
  display: block;
  margin-bottom: 25px;
}
.footer .footer_box .left .content span {
  display: block;
  font-size: 15px;
}

.footer .footer_box .left .content span:first-child {
  margin-bottom: 15px;
}

.footer .footer_box .left .content span a {
  color: #fff;
  margin-left: 5px;
}

.footer .footer_box .center .content .post_container {
  padding: 16px;
  box-sizing: border-box;
  border-bottom: 1px solid #fff;
}
.footer .footer_box .center .content {
  margin-top: -10px;
}
.footer .footer_box .center .content .post_container:last-child {
  border-bottom: 0;
}
.footer .footer_box .center .content .post_container:hover {
  background-color: #ccc;
}
.footer .footer_box .center .content .post_container .post_img {
  width: 50px;
  height: 50px;
  display: inline-block;
  vertical-align: middle;
  margin-right: 15px;
}
.footer .footer_box .center .content .post_container .post_info {
  display: inline-block;
  vertical-align: middle;
}
.footer .footer_box .center .content .post_container .post_info span {
  display: block;
  line-height: 22px;
  font-size: 18px;
}
.footer
  .footer_box
  .center
  .content
  .post_container
  .post_info
  span:last-child {
  font-size: 15px;
}
.footer .footer_box .right .content .tag {
  display: inline-block;
  background-color: #9e9e9e;
  color: #000;
  padding: 0 8px;
  margin-right: 5px;
  margin-bottom: 15px;
  font-size: 12px;
}
.footer .footer_box .right .content .insert {
  font-size: 15px;
  background-color: #000;
  color: #fff;
}
.footer .footer_end {
  background-color: #000;
  padding: 24px 0;
  color: #fff;
  text-align: center;
}
.footer .footer_end a {
  color: #fff;
  margin-left: 5px;
}

@media (max-width: 600px) {
  .sideMenu {
    display: none;
  }
  .header .top {
    display: block;
  }
  .sideMenu .close_ico {
    display: block;
  }
  .footer .footer_box .content_box {
    width: 100%;
    display: block;
    margin-bottom: 30px;
  }
  .footer .footer_box .content_box .title {
    margin-bottom: 10px;
  }
}

/* MainPageComponent */
.mainpage_wrapper {
	/* width: 100%; */
	height: 100vh;
	padding: 16px 0;
	vertical-align: top;
	margin-left: 300px;
	position: relative;
}
@media (max-width: 992px) {
	.mainpage_wrapper {
		margin-left: 0;
	}
}

/* PortfolioList */
.portfolio_list {
	padding: 0 16px;
}

/* TechnicalSkills */
.technical_skills {
	padding: 0 16px;
}

/* ContactMe */
.contactme {
	background-color: #9e9e9e;
	padding: 12px 0;
}
.contactme .contactme_box {
	padding: 0 16px;
}
/* ContactInformation */
.contact_information {
	margin: 24px 0;
}
.contact_information_list {
	width: 33.3%;
	background-color: #009688;
	display: inline-block;
	vertical-align: middle;
	padding: 15px 0;
	text-align: center;
}
.contact_information_list:nth-child(odd) {
	background-color: #616161;
}
.contact_information_list .address_ico {
	width: 36px;
	height: 36px;
	margin: 0 auto;
	margin-bottom: 15px;
}
.contact_information_list .contact {
	font-size: 15px;
	color: #fff;
}
@media (max-width: 600px) {
	.contact_information_list {
		display: block;
		width: 100%;
	}
}

/* ContactInputList */
.contact_input {
	margin-bottom: 16px;
}
.contact_input button {
	padding: 8px 16px;
	font-size: 15px;
	border: 0;
	background-color: #000;
	color: #fff;
	cursor: pointer;
}
.contact_input button img {
	margin-right: 15px;
	display: inline-block;
	vertical-align: middle;
}
.contact_input button span {
	display: inline-block;
	vertical-align: middle;
}
.contact_input_list {
	margin-bottom: 16px;
}
.contact_input_list span {
	font-size: 15px;
}
.contact_input_list input {
	width: 100%;
	line-height: 22.5px;
	border: 1px solid #ccc;
	padding: 8px;
	display: block;
	box-sizing: border-box;
}

/* table */
.table_container {
  padding: 0 16px;
  margin-bottom: 40px;
}

.table_container table {
  width: 100%;
  margin-top: 25px;
  border-spacing: 0;
  font-size: 15px;
}
.table_container th {
  height: 25px;
}

.table_container table tbody tr td:first-child {
  padding: 5px 16px;
}
.table_container table tbody tr td:last-child {
  text-align: right;
  padding-right: 16px;
}
.table_container table tbody tr .title {
  width: 100%;
}
.table_container td {
  padding: 5px;
  height: 30px;
}
.table_container tbody tr:nth-child(odd) {
  background-color: #fff;
}

body {
	margin: 0;
	background-color: #f1f1f1;
	font-family: 'Raleway', sans-serif;
}

code {
	font-family: 'Raleway', sans-serif;
}
hr {
	border: 0;
	border-top: 1px solid #eee;
	margin: 35px 0;
}

